@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  

}

.navSection {
  height: 45px;
  width: 100%;
  position: absolute;
  background-color: rgba(255, 255, 255, 0.116);
  -webkit-backdrop-filter: blur(3px);
          backdrop-filter: blur(3px); 
  display: -webkit-flex; 
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.navBar {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 40px;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  grid-gap: 14px;
  gap: 14px;
  font-weight: 600;
  position: -webkit-sticky;
  position: sticky;
}
.navElements{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.fa-superpowers{
  -webkit-animation: logo 5s infinite;
          animation: logo 5s infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}
@-webkit-keyframes  logo {
  0%{
  -webkit-transform: rotateZ(0deg);
          transform: rotateZ(0deg);

}
  100%{
  -webkit-transform: rotateZ(-360deg);
          transform: rotateZ(-360deg);

}
}
@keyframes  logo {
  0%{
  -webkit-transform: rotateZ(0deg);
          transform: rotateZ(0deg);

}
  100%{
  -webkit-transform: rotateZ(-360deg);
          transform: rotateZ(-360deg);

}
}
@media screen and (min-width:700px) {
  .logoFor--700 {
      display: none;
      
  }
  


}

@media screen and (max-width:700px) {
   

   

  .logoFor-700 {
      display: none;
      
    }
  
 
    


  .navBar {
      width: 100%;
      position: absolute;
      height: auto;
      top: -140px; 
      display: -webkit-flex; 
      display: flex;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      -webkit-justify-content: center;
              justify-content: center;
      -webkit-align-items: center;
              align-items: center;
      grid-column-gap: 20px;
      -webkit-column-gap: 20px;
              column-gap: 20px; 
      transition: all 0.5s ease-in-out;
      z-index: -1; 
      background-color: rgba(68, 68, 68, 0.315);
      -webkit-backdrop-filter: blur(1px);
              backdrop-filter: blur(1px); 
      padding: 15px 0px; 

  }
  .navElements{
    
    color: rgb(251, 251, 251);
  }
  
  .bars{
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: column;
             flex-direction: column;
     position: absolute;
     grid-gap: 2px;
     gap: 2px; 
     top: 13px;
     left: 10px;
     transition: all 0.1s ease-in-out;
     z-index: 3;
      
     
  
  }
  .bar{
    width: 23px;
    height: 5px;
    border-radius: 50px;
    background-color: black;
    transition: all 0.1s ease-in-out;
  }

}




/*                                    Main img section                       */

.MainImgDiv{
  
  width: 100%;
  height: 100vh;
  position:relative;
  overflow: hidden;
   
   

}
.MainImg{
  width: 100%;
  height: 100vh;
  position:relative;
  
  background-image: url(/tophills/static/media/toa-heftiba-Fv2yqBcRkqg-unsplash.2f90f68a.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -10;
  -webkit-animation: MainImg_Animation 10s infinite alternate;
          animation: MainImg_Animation 10s infinite alternate; 
  -webkit-animation-timing-function: linear; 
          animation-timing-function: linear;
}
@-webkit-keyframes MainImg_Animation {
  0%{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
  100%{
    -webkit-transform: scale(1.2,1.2);
            transform: scale(1.2,1.2);
  }
}
@keyframes MainImg_Animation {
  0%{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
  }
  100%{
    -webkit-transform: scale(1.2,1.2);
            transform: scale(1.2,1.2);
  }
}
.loginPanal{
  position: absolute;
  top: 25vh;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content:  center;
          justify-content:  center;
  -webkit-align-items: center;
          align-items: center;
  z-index: 2;
  grid-gap: 20px;
  gap: 20px;
}
.loginPanal h1{
  font-family: 'Pacifico', cursive;
  font-size: 60px;
  font-weight: 900; 
  background-image: linear-gradient(270deg, #ff81ff 0, #eb72ff 25%, #cf61ff 50%, #b151f6 75%, #9346ef 100%);
